window.toggleAbstractDisplay = function(id) {
    const el = document.getElementById(id);
    const unfold = el.getAttribute('unfold');
    if (unfold && unfold === 'true') {
        el.style['max-height'] = '0';
        el.setAttribute('unfold', 'false');
    } else {
        const div = el.querySelector('div');
        el.style['max-height'] = div.getBoundingClientRect().height + 'px';
        el.setAttribute('unfold', 'true');
    }
}
